import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import TextFieldLite from '../../../components/common/TextFieldLite';
import PlusRoundButton from '../../../components/common/PlusRoundButton';
import {useDispatch, useSelector} from 'react-redux';
import {addClient,showWhatsApp,showDeliver} from '../../../actions/clients/makeClientActions';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import PurpleDatePicker from '../../../components/common/PurpleDatePicker';
import PurpleTimePicker from '../../../components/common/PurpleTimePicker';
import InputAdornment from '@material-ui/core/InputAdornment';
import client_phone from '../../../images/client_phone.svg';
import truck from '../../../images/truck.svg';
import whatsapp from '../../../images/whatsapp_small.svg';
import PurpleCheckbox from '../../../components/common/PurpleCheckbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';


const MakeOrderMakeClient = ({showSaved}) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [saveButtonDisabled, setSaveButtonDisabled] = useState(false);
  const [socialNetworks, setSocialNetworks] = useState(['1']);
  const [deliverCheckbox, setDeliverCheckbox] = useState(false);
  const [networkCheckbox, setNetworkCheckbox] = useState(false);
  const client = useSelector(
      state => state.clients.makeClient);

  const whatsApp = useSelector(
      state => state.clients.makeClient.showWhatsApp);

  const showWhatsAppHandler = (event) => {
    dispatch(showWhatsApp(event.target.checked))
  };


  const deliver = useSelector(
      state => state.clients.makeClient.showDeliver);

  const showDeliverHandler = (event) => {
    dispatch(showDeliver(event.target.checked))
  } ;

  const clientChange = (event) => {
    dispatch(addClient({[event.target.name]: event.target.value}));
  };

  const clickHandle = () => {
    showSaved(true);
  };

  const addNetwork = () => {
    console.log();
    setSocialNetworks((prevState) => [...prevState, '']);
  };

  const networkChange = (event, i) => {
    setSocialNetworks((prevState) => {
      prevState[i] = event.target.value;
      return [...prevState];
    })
  }

  const dateChange = (e) => {
    var months = ["Янв", "Фев", "Мар", "Апр", "Май", "Июн", "Июл", "Авг", "Сен", "Окт", "Ноя", "Дек"];
    var days = ["Вс", "Пн", "Вт", "Ср", "Чт", "Пт", "Сб"];
    const fullDate = e.getDate() + '.' + months[e.getMonth()] + '('+ days[e.getDay()]+')' +'.' + e.getFullYear();
    dispatch(addClient({date: JSON.stringify({human: fullDate, full: e})}));
  }

  const timeChange = (e) => {
    const minutes = e.getMinutes();
    const fullTime = e.getHours() + ':' + ((minutes < 10) ? '0' + minutes : minutes);
    dispatch(addClient({time: JSON.stringify({human: fullTime, full: e})}));
  }

  return (
      <div className={classes.makeClientBlock}>
        <TextFieldLite label="Имя"
                       required
                       size="small"
                       variant="outlined"
                       name="name"
                       className={classes.clientName}
                       type='text'
                       value={client.name}
                       onChange={clientChange}
                       InputProps={{
                         endAdornment: (
                             <InputAdornment position="end">
                               <img src={client_phone} className={classes.clientPhoneIconClock}/>
                             </InputAdornment>
                         ),
                       }}
        />

        <div className={classes.twoLine}>
          <div  className={classes.twoLineDate}>
            <PurpleDatePicker getDate={dateChange} />
          </div>
          <div className={classes.clientDate}>
            <PurpleTimePicker getTime={timeChange} />
          </div>
        </div>

        <div className={classes.thirdLine}>
          <TextFieldLite label="Адрес"
                         required
                         size="small"
                         variant="outlined"
                         name="address"
                         disabled = {deliver ? false : true}
                         className={classes.clientAddress}
                         type='text'
                         value={client.address}
                         onChange={clientChange}/>

                  <div className={classes.deliveryCheckboxWrapper}>
                    <img src={truck} className={classes.truckIcon}/>
                    <FormControlLabel
                        control={
                          <PurpleCheckbox
                              checked={ deliver.date && JSON.parse(deliver.date).full}
                              onChange={showDeliverHandler}
                              name="deliver"
                          />
                        }
                    />
                  </div>


        </div>
        <div className={classes.fourthLine}>
          <TextFieldLite label="Телефон"
                         required
                         size="small"
                         name="phone"
                         variant="outlined"
                         value={client.phone}
                         className={classes.clientPhone}
                         onChange={clientChange}
                         type='text'/>

          <div className={classes.networkCheckboxWrapper}>
            <img src={whatsapp} className={classes.whatsappIcon}/>
            <FormControlLabel
                control={
                  <PurpleCheckbox
                      checked={ whatsApp.date && JSON.parse(whatsApp.date).full}
                      onChange={showWhatsAppHandler}
                      name="WhatsApp"
                  />
                }
            />
          </div>
        </div>
        <div className={classes.fifthLine}>

          {Array.isArray(socialNetworks) &&
          socialNetworks.map((elem, i) => <TextFieldLite label="Соц.сеть"
                                                         required
                                                         size="small"
                                                         name="socialNetworks[0]"
                                                         variant="outlined"
                                                         value={elem}
                                                         onChange={(e) => networkChange(e, i)}
                                                         className={classes.clientSocialNetwork}
                                                         type='text'/>)
          }

        </div>

        <IconButton className={classes.addButton}>
          <PlusRoundButton addLine={addNetwork}/>
        </IconButton>
        <div className={classes.buttonSaveBlock}>
          <Button size='medium'
                  onClick={clickHandle}
                  disabled={saveButtonDisabled}
                  variant='contained'
                  className={classes.materialTypeButton}
          >Сохранить</Button>
        </div>
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  makeClientBlock: {
    flexBasis: '15%',
    border: '1px solid #a7a0a0',
    boxShadow: '0px 10px 15px -5px rgba(0,0,0,0.5)',
    padding: '3%',
    paddingTop: '0',
    flexWrap: 'wrap',
    maxWidth: '380px',
    height: 'fit-content',
    marginRight: '20px',
    marginLeft: '15px',
    ['@media (max-width:1300px)']: {
      flexBasis: '100%',
      maxWidth: 'initial',
      marginBottom: '20px',
    },
    ['@media (max-width:668px)']: {
      border: 'none',
      boxShadow: 'none',
      margin: '0 5px',
    },
  },
  clientName: {
    marginTop: '20px',
    width: '100%',
  },
  clientDate: {
    flexBasis: '37%',
  },
  twoLine: {
    display: 'flex',
    marginTop: '20px',
    justifyContent: 'space-between',
  },
  thirdLine: {
    display: 'flex',
    marginTop: '20px',
    justifyContent: 'space-between',
  },
  fourthLine: {
    display: 'flex',
    marginTop: '20px',
    justifyContent: 'space-between',
  },
  fifthLine: {
    display: 'flex',
    marginTop: '20px',
    justifyContent: 'space-between',
    alignItems: 'flex-start',
    flexDirection: 'column'
  },
  clientAddress: {
    flexBasis: '65%',
  },
  clientTime: {
    flexBasis: '30%',
  },
  clientPhone: {
    flexBasis: '65%',
  },
  clientSocialNetwork: {
    width: '65%',
    marginBottom: '10px'
  },
  buttonSaveBlock: {
    borderTop: '1px solid #c1bdbdde',
    marginTop: '60px',
    paddingTop: '20px',
    display: 'flex',
    justifyContent: 'flex-end',
  },
  materialTypeButton: {
    background: global.config.colors.purple,
    color: '#fff',
    borderRadius: '13px',
    justifyContent: 'center',
    alignItems: 'center',
    fontWeight: '300',
    padding: '1px 10px',
    cursor: 'pointer',
    '&:hover': {
      background: global.config.colors.purple,
      color: global.config.colors.gold,
    },
    [theme.breakpoints.down('xs')]: {
      display: 'flex',
    },
  },
  addButton: {
    float: 'right'
  },
  twoLineDate: {
    flexBasis: '55%'
  },
  clientPhoneIconClock: {
    width: '20px'
  },
  truckIcon: {
    width: '20px'
  },
  whatsappIcon: {
    width: '20px'
  },
  deliveryCheckboxWrapper: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    width: '22%'
  },
  networkCheckboxWrapper: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    width: '22%'
  },
}));

export default MakeOrderMakeClient;
