import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import PurpleCheckbox from '../../../components/common/PurpleCheckbox';
import TextFieldLite from '../../../components/common/TextFieldLite';
import Slider from '@material-ui/core/Slider';
import truck from '../../../images/truck.svg';
import whatsapp from '../../../images/whatsapp_small.svg';
import phone from '../../../images/phone_small.svg';
import instagram from '../../../images/instagram_small.svg';
import DownArrow from '../../../components/common/DownArrow';
import {useSelector} from 'react-redux';
import InputAdornment from '@material-ui/core/InputAdornment';
import Input from '@material-ui/core/Input';

const MakeOrderClientCreated = ({isOpen}) => {
  const classes = useStyles();
  const [confirmClient, setConfirmClient] = useState('');
  const [reminder, setReminder] = useState('');
  const client = useSelector(
      state => state.clients.makeClient);

  const whatsApp = useSelector(
      state => state.clients.makeClient.showWhatsApp);
  const deliver = useSelector(
      state => state.clients.makeClient.showDeliver);

  const [value, setValue] = useState('');

  const handleSliderChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleInputChange = (event) => {
    setValue(event.target.value === '' ? '' : Number(event.target.value));
  };

  const handleBlur = () => {
    if (value < 0) {
      setValue(0);
    } else if (value > 100) {
      setValue(100);
    }
  };

  const changeReminder = (event, newValue) => {
    setReminder(newValue);
  };

  return (
      <div className={classes.makeClientCreatedBlock}>
        <div className={classes.lineOne}>
          <h3>
            {client.name}
          </h3>
          <div>
            Подтверждён
            <PurpleCheckbox
                checked={confirmClient}
                onChange={() => setConfirmClient(!confirmClient)}
                name="confirmClient"
            />
          </div>
        </div>
        <div className={classes.lineTwo}>
          <div className={classes.lineTwoFirstColumn}>
            <div className={classes.lineTwoFirstColumnDiv}>
              <img src={whatsapp} className={whatsApp ?
                  classes.whatsappIcon :
                  classes.whatsappIconDisabled}/>
              <img src={phone} className={classes.phoneIcon}/>
              <span>
               {client.phone}
              </span>
            </div>
            <div className={classes.lineTwoFirstColumnDivSecond}>
              <img src={instagram} className={classes.instagramIcon}/>
              <span>
                Переписка
              </span>
            </div>
          </div>
          <div className={classes.lineTwoSecondColumn}>
            <div className={classes.lineTwoSecondColumnDiv}>
              {client.date && JSON.parse(client.date).human}
            </div>
            <div className={classes.lineTwoSecondColumnDivSecond}>
              {client.time && JSON.parse(client.time).human}
            </div>
          </div>
          <div className={classes.lineTwoThirdColumn}>
            <TextFieldLite disabled={confirmClient ? false : true}
                           label="ПО"
                           required size="small"
                           variant="outlined"
                           className={classes.po}
                           type='number'/>
          </div>
        </div>
        <div className={classes.lineThree}>
          <div className={classes.lineThreeFirstColumn}>
            <div className={classes.reminderLabel}>
              <div>Напомнить</div>
              <div>? Дня</div>
            </div>
            <Slider color='secondary'
                    className={classes.slider}
                    value={reminder}
                    onChange={changeReminder}
                    max={30}
                    min={0}
                    step={3}
                    value={typeof value === 'number' ? value : 0}
                    onChange={handleSliderChange}
                    aria-labelledby="input-slider"
            />
          </div>
          <div className={classes.lineThreeSecondColumn}>
            <Input
                color='secondary'
                endAdornment={<InputAdornment
                    position="end">Дн.</InputAdornment>}
                value={value}
                margin="dense"
                onChange={handleInputChange}
                onBlur={handleBlur}
                inputProps={{
                  step: 3,
                  min: 0,
                  max: 30,
                  type: 'number',
                  'aria-labelledby': 'input-slider',
                }}
            />
          </div>
        </div>
        <div className={classes.lineFour}>
          <div>
            <img src={truck} className={deliver ?
                classes.truckIcon :
                classes.truckIconDisabled}/>
          </div>
          <div>
            <span>{client.address}</span>
          </div>
          <div className={classes.arrowBlock}>
            <DownArrow isOpen={isOpen}/>
          </div>
        </div>
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  makeClientCreatedBlock: {
    border: '1px solid #a7a0a0',
    boxShadow: '0px 10px 15px -5px rgba(0,0,0,0.5)',
    padding: '3%',
    paddingTop: '0',
    flexWrap: 'wrap',
    maxWidth: '380px',
    height: 'fit-content',
    marginRight: '20px',
    marginLeft: '15px',
    ['@media (max-width:1300px)']: {
      flexBasis: '100%',
      maxWidth: 'initial',
      marginBottom: '20px',
    },
    ['@media (max-width:668px)']: {
      border: 'none',
      boxShadow: 'none',
      margin: '0 5px',
    },
  },
  lineOne: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  Checkbox: {
    marginRight: '20px',
  },
  lineTwo: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  lineThree: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    marginTop: '10px',
    marginLeft: '5px',
  },
  lineFour: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    marginTop: '10px',
  },
  lineFourFirstColumn: {
    paddingRight: '25px',
  },
  lineTwoFirstColumn: {
    flexBasis: '40%',
    color: '#9A157D',
  },
  lineTwoSecondColumn: {
    flexBasis: '30%',
    color: '#FA1919',
    fontWeight: 'bold',
  },
  truckIconDisabled:{
    width: '20px',
    display: 'none',
  },
  lineTwoThirdColumn: {
    flexBasis: '25%',
  },
  lineThreeFirstColumn: {
    flexBasis: '90%',
  },
  truckIcon: {
    width: '20px',
  },
  whatsappIconDisabled: {
    paddingRight: '2px',
    display: 'none',
  },
  lineThreeSecondColumn: {
    flexBasis: '45%',
    marginLeft: '80px',
  },
  whatsappIcon: {
    paddingRight: '2px',
    width: '15px',
  },
  lineTwoSecondColumnDivSecond: {
    marginBottom: '20px',
    color: '#9A157D',
    flexBasis: '65%',
  },
  instagramIcon: {
    paddingRight: '5px',
  },
  phoneIcon: {
    paddingRight: '5px',
  },
  lineTwoFirstColumnDiv: {
    marginBottom: '7px',
    display: 'flex',
    alignItems: 'center',
  },
  lineTwoFirstColumnDivSecond: {
    display: 'flex',
    alignItems: 'center',
  },
  lineTwoSecondColumnDiv: {
    marginBottom: '7px',
  },
  reminderLabel: {
    display: 'flex',
    justifyContent: 'space-between',
    fontSize: '11px',
    fontWeight: 'bold',
  },
  arrowBlock: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  slider: {
    color: global.config.colors.purple,
    '&:hover': {
      color: global.config.colors.red,
    },
  },

}));

export default MakeOrderClientCreated;
